import React from 'react';
import { Link } from 'react-router-dom';
var SkillCard = function (_a) {
    var id = _a.id, title = _a.title, content = _a.content, image = _a.image, deleteSkill = _a.deleteSkill;
    return (<div className="card-wrapper">
      <img src={image}/>
      <div>
        <h2>{title}</h2>
        <p>{content}</p>
      </div>
      <div className="edit-wrapper">
        <Link to={"/admin/skills/" + id}>
          <i className="fa fa-pencil"/>
        </Link>
        <button className="delete-btn" onClick={function () { return deleteSkill; }}>
          <i className="fa fa-times"/>
        </button>
      </div>
  </div>);
};
export default SkillCard;
