import React from 'react';
import { Link } from 'react-router-dom';
var WorkCard = function (_a) {
    var id = _a.id, title = _a.title, content = _a.content, image = _a.image, deleteWorkItem = _a.deleteWorkItem;
    return (<div className="card-wrapper">
      <img src={image}/>
      <div>
        <h2 className="card-title">{title}</h2>
        <a href={content} target="_blank">{content}</a>
      </div>
      <div className="edit-wrapper">
        <Link to={"/admin/work/" + id}>
          <i className="fa fa-pencil"/>
        </Link>
        <button className="delete-btn" onClick={function () { return deleteWorkItem; }}>
          <i className='fa fa-times'/>
        </button>
      </div>
  </div>);
};
export default WorkCard;
